	module.exports = {"data":{"markdownRemark":{"html":"<p>Translation is an all-or-nothing proposal.</p>\n<p>We won’t be able to add new languages to Free Code Camp until all of our<br>\nchallenges are translated into that language.</p>\n<p>In addition to translating these initially, we’ll also need to maintain<br>\nthe translation as the challenges are gradually updated (we use the<br>\ngithub issue system to keep record).</p>\n<p>If you’re able to help us, please join the <a href='https://gitter.im/FreeCodeCamp/Translators' target='_blank' rel='nofollow'>gitter channel</a><br>\nand read the <a href='http://forum.freecodecamp.com/t/guidelines-for-translating-free-code-camp-to-any-language/19111' target='_blank' rel='nofollow'>Translation Guide</a></p>","fields":{"slug":"/miscellaneous/what-to-do-if-you-speak-a-language-not-yet-supported-by-free-code-camp/"},"frontmatter":{"title":"What to Do if You Speak a Language not Yet Supported by Free Code Camp"}}},"pathContext":{"slug":"/miscellaneous/what-to-do-if-you-speak-a-language-not-yet-supported-by-free-code-camp/"}}

